.btn {
  display: block;
  width: max-content;
  cursor: pointer;
  transition: opacity $trans-default;

  @include hover-focus {
    opacity: 0.8;
  }
}

.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}

.main-block {
  width: 100%;
  min-height: 306px;
  background-color: #93bbea;
  border-radius: 40px;
  padding: 40px 0;
  overflow: hidden;
  position: relative;
  margin-bottom: 82px;
  margin-top: 40px;

  display: flex;
  justify-content: space-between;

  @include vp-1023 {
    padding: 120px 12px;
    margin-top: 20px;
  }

  @include vp-767 {
    margin-bottom: 40px;
    margin-top: 16px;
  }

  &__menu-list {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: flex;
    margin-left: -10px;
    margin: 0 0 0 -8px;
    flex-wrap: wrap;

    @include vp-767 {
      flex-direction: column;
      margin: 0;
      width: 100%;
    }
  }

  &__menu-item {
    margin-left: 8px;
    margin-bottom: 8px;

    @include vp-767 {
      width: 100%;
      margin-left: 0;
    }
  }

  &__btn {
    min-width: 160px;
    min-height: 48px;
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: center;
    color: #1a1a1a;
    background-color: #ffffff;
    width: max-content;
    border-radius: 12px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;

    @include vp-767 {
      width: 100%;
    }

    svg {
      order: -1;
      margin-right: 8px;
    }
  }

  &__title {
    font-size: 60px;
    font-weight: 700;
    line-height: 72px;
    letter-spacing: 0px;
    text-align: center;
    margin: 0 0 3px 0;
    letter-spacing: 1.3px;

    @include vp-767 {
      letter-spacing: unset;
      font-size: 42px;
      line-height: 40px;
    }
  }

  &__text {
    font-size: 17px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: center;
    margin: 0 0 41px 0;
    max-width: 566px;
    letter-spacing: 0.2px;
  }

  &__content-area {
    max-width: 670px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-767 {
      max-width: unset;
    }
  }

  &__img-first,
  &__img-two {
    svg {
      position: absolute;
    }
  }

  &__img-two {
    svg {
      right: 0;
      bottom: 0;
      width: 277px;
      height: 226px;

      @include vp-1279 {
        width: 15.6vw;
        height: 13.5vw;
      }

      @include vp-1023 {
        width: 152px;
        height: 124px;
      }
    }
  }

  &__img-first {
    svg {
      left: -6px;
      top: 37px;
      width: 275px;
      height: 195px;

      @include vp-1279 {
        width: 12.5vw;
        height: 9.3vw;
      }

      @include vp-1023 {
        width: 110px;
        height: 78px;
      }
    }
  }

  &__menu-item--chat {
    svg {
      width: 14px;
      height: 13px;
    }
  }

  &__menu-item--call {
    svg {
      width: 13px;
      height: 13px;
    }
  }

  &__menu-item--mail {
    svg {
      width: 14px;
      height: 11px;
    }
  }

  &__menu-item--responce {
    svg {
      width: 18px;
      height: 16px;
    }
  }
}


.social {
  padding: 0 102px;
  margin-bottom: 80px;

  @include vp-1279 {
    padding: 0 6vw;
  }

  @include vp-767 {
    padding: 0;
    margin-bottom: 50px;
  }

  &__wrap {
    width: 100%;
    min-height: 166px;
    box-shadow: 0px 20px 60px 0px #1a1a1a0d;
    display: flex;
    padding: 42px 40px;
    border-radius: 40px;
    justify-content: space-between;

    @include vp-1279 {
      padding: 42px 20px;
    }

    @include vp-1023 {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
    }

    @include vp-1023 {
      padding: 20px 20px;
    }

    @include vp-767 {
      padding: 20px 12px;
    }
  }

  &__title {
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: 0px;
    margin: 0 0 17px 0;

    @include vp-1023 {
      text-align: center;
    }
  }

  &__slogan {
    font-size: 40px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0px;
    margin: 0;

    @include vp-1023 {
      text-align: center;
    }

    @include vp-767 {
      font-size: 30px;
      line-height: 32px;
    }
  }

  &__icons-list {
    list-style: none;
    padding: 13px 0 0 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;

    @include vp-767 {
      justify-content: center;
    }
  }

  &__icons-item {
    margin-left: 14px;
    margin-bottom: 14px;

    @include vp-1279 {
      margin-left: 12px;
      margin-bottom: 12px;
    }
  }

  &__btn {
    width: 56px;
    height: 56px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    margin-right: 10px;

    @include vp-1023 {
      margin: 0;
    }
  }
}

.faq {
  overflow: hidden;
  margin-bottom: 29px;
  position: relative;
  padding-bottom: 50px;

  @include vp-767 {
    margin-bottom: 10px;
  }

  &__btn {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    position: absolute;
    border: none;
    right: 3px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    cursor: pointer;
    padding: 0;
    background-color: unset;
    overflow: visible;

    svg {
      width: 51px;
      height: 58px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%, -50%, 0);
      transition: transform $trans-default;
    }

    @include hover-focus {
      svg {
        transform: translate3d(-48%, -50%, 0);
      }
    }

    @include vp-767 {
      top: -56px;
      transform: translateY(0);
    }
  }

  &__wrapper {
    position: relative;
  }

  &__btn--left {
    right: unset;
    left: -19px;
    transform: scale(-1, 1) translate3d(-50%, -50%, 0);

    @include vp-767 {
      top: -56px;
      transform: scale(-1, 1) translate3d(0, 0, 0);
      left: 4px;
    }
  }

  &__title {
    font-size: 40px;
    font-weight: 700;
    line-height: 48px;
    text-align: center;
    margin: 0 0 31px 0;

    @include vp-767 {
      font-size: 30px;
      line-height: 32px;
      margin-bottom: 65px;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    width: max-content;
  }

  &__item {
    width: 282px;
    min-height: 160px;
    box-shadow: 0px 0px 30px 0px #0000000d;
    border-radius: 16px;
    //margin-right: 24px;
  }

  &__item-link {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;

    svg {
      width: 104px;
      height: 162px;
      flex-shrink: 0;
    }
  }

  &__item-info {
    padding: 16px;
  }

  &__item-title {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin: 0 0 9px 0;
  }

  &__item-text {
    font-size: 15px;
    font-weight: 300;
    line-height: 21px;
    color: #949494;
    margin: 0;
  }
}

.reference {
  margin-bottom: 80px;

  &__title {
    font-size: 40px;
    font-weight: 700;
    line-height: 48px;
    text-align: center;
    margin: 0 11px 31px 0;

    @include vp-767 {
      font-size: 30px;
      line-height: 32px;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0 102px;

    @include vp-1279 {
      padding: 0 6vw;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &__item {
    box-shadow: 0px 0px 30px 0px #0000000d;
    min-height: 64px;
    margin-bottom: 12px;
    border-radius: 16px;
  }

  &__item-title {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin: 0 0 4px 0;
    //letter-spacing: -0.6px;
  }

  &__item-text {
    font-size: 13px;
    font-weight: 300;
    line-height: 18px;
    color: #949494;
    margin: 0 0 13px 0;
    letter-spacing: 0.1px;
    max-width: 45%;

    @include vp-1279 {
      max-width: 65%;
    }

    @include vp-1023 {
      max-width: 80%;
    }

    @include vp-767 {
      max-width: 95%;
    }
  }

  &__link {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 20px 40px 8px 24px;
    min-height: inherit;

    svg {
      width: 24px;
      height: 24px;
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 16px;
    }
  }
}
