@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/museosanscyrl-300.woff2") format("woff2"), url("../fonts/museosanscyrl-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/museosanscyrl-500.woff2") format("woff2"), url("../fonts/museosanscyrl-500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/museosanscyrl-700.woff2") format("woff2"), url("../fonts/museosanscyrl-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/museosanscyrl-900.woff2") format("woff2"), url("../fonts/museosanscyrl-900.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  font-family: "Museo Sans Cyrl", sans-serif;
  color: #1a1a1a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #1a1a1a;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}
.wrapper__bg-container {
  position: absolute;
  width: 100%;
  height: 100%;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
  position: relative;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: block;
  width: max-content;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}

.main-block {
  width: 100%;
  min-height: 306px;
  background-color: #93bbea;
  border-radius: 40px;
  padding: 40px 0;
  overflow: hidden;
  position: relative;
  margin-bottom: 82px;
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .main-block {
    padding: 120px 12px;
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .main-block {
    margin-bottom: 40px;
    margin-top: 16px;
  }
}
.main-block__menu-list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: flex;
  margin-left: -10px;
  margin: 0 0 0 -8px;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .main-block__menu-list {
    flex-direction: column;
    margin: 0;
    width: 100%;
  }
}
.main-block__menu-item {
  margin-left: 8px;
  margin-bottom: 8px;
}
@media (max-width: 767px) {
  .main-block__menu-item {
    width: 100%;
    margin-left: 0;
  }
}
.main-block__btn {
  min-width: 160px;
  min-height: 48px;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0px;
  text-align: center;
  color: #1a1a1a;
  background-color: #ffffff;
  width: max-content;
  border-radius: 12px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
}
@media (max-width: 767px) {
  .main-block__btn {
    width: 100%;
  }
}
.main-block__btn svg {
  order: -1;
  margin-right: 8px;
}
.main-block__title {
  font-size: 60px;
  font-weight: 700;
  line-height: 72px;
  letter-spacing: 0px;
  text-align: center;
  margin: 0 0 3px 0;
  letter-spacing: 1.3px;
}
@media (max-width: 767px) {
  .main-block__title {
    letter-spacing: unset;
    font-size: 42px;
    line-height: 40px;
  }
}
.main-block__text {
  font-size: 17px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: center;
  margin: 0 0 41px 0;
  max-width: 566px;
  letter-spacing: 0.2px;
}
.main-block__content-area {
  max-width: 670px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 767px) {
  .main-block__content-area {
    max-width: unset;
  }
}
.main-block__img-first svg, .main-block__img-two svg {
  position: absolute;
}
.main-block__img-two svg {
  right: 0;
  bottom: 0;
  width: 277px;
  height: 226px;
}
@media (max-width: 1279px) {
  .main-block__img-two svg {
    width: 15.6vw;
    height: 13.5vw;
  }
}
@media (max-width: 1023px) {
  .main-block__img-two svg {
    width: 152px;
    height: 124px;
  }
}
.main-block__img-first svg {
  left: -6px;
  top: 37px;
  width: 275px;
  height: 195px;
}
@media (max-width: 1279px) {
  .main-block__img-first svg {
    width: 12.5vw;
    height: 9.3vw;
  }
}
@media (max-width: 1023px) {
  .main-block__img-first svg {
    width: 110px;
    height: 78px;
  }
}
.main-block__menu-item--chat svg {
  width: 14px;
  height: 13px;
}
.main-block__menu-item--call svg {
  width: 13px;
  height: 13px;
}
.main-block__menu-item--mail svg {
  width: 14px;
  height: 11px;
}
.main-block__menu-item--responce svg {
  width: 18px;
  height: 16px;
}

.social {
