  display: flex;
  padding: 42px 40px;
  border-radius: 40px;
  justify-content: space-between;
}
@media (max-width: 1279px) {
  .social__wrap {
    padding: 42px 20px;
  }
}
@media (max-width: 1023px) {
  .social__wrap {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}
@media (max-width: 1023px) {
  .social__wrap {
    padding: 20px 20px;
  }
}
@media (max-width: 767px) {
  .social__wrap {
    padding: 20px 12px;
  }
}
.social__title {
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0px;
  margin: 0 0 17px 0;
}
@media (max-width: 1023px) {
  .social__title {
    text-align: center;
  }
}
.social__slogan {
  font-size: 40px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 0px;
  margin: 0;
}
@media (max-width: 1023px) {
  .social__slogan {
    text-align: center;
  }
}
@media (max-width: 767px) {
  .social__slogan {
    font-size: 30px;
    line-height: 32px;
  }
}
.social__icons-list {
  list-style: none;
  padding: 13px 0 0 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .social__icons-list {
    justify-content: center;
  }
}
.social__icons-item {
  margin-left: 14px;
  margin-bottom: 14px;
}
@media (max-width: 1279px) {
  .social__icons-item {
    margin-left: 12px;
    margin-bottom: 12px;
  }
}
.social__btn {
  width: 56px;
  height: 56px;
}
.social__btn svg {
  width: 100%;
  height: 100%;
}
.social__info {
  margin-right: 10px;
}
@media (max-width: 1023px) {
  .social__info {
    margin: 0;
  }
}

.faq {
  overflow: hidden;
  margin-bottom: 29px;
  position: relative;
  padding-bottom: 50px;
}
@media (max-width: 767px) {
  .faq {
    margin-bottom: 10px;
  }
}
.faq__btn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  position: absolute;
  border: none;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  cursor: pointer;
  padding: 0;
  background-color: unset;
  overflow: visible;
}
.faq__btn svg {
  width: 51px;
  height: 58px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  transition: transform 0.3s ease;
}
@media (hover: hover) {
  .faq__btn:hover:not(.focus-visible) svg {
    transform: translate3d(-48%, -50%, 0);
  }
}
.faq__btn.focus-visible:focus svg {
  transform: translate3d(-48%, -50%, 0);
}
@media (max-width: 767px) {
  .faq__btn {
    top: -56px;
    transform: translateY(0);
  }
}
.faq__wrapper {
  position: relative;
}
.faq__btn--left {
  right: unset;
  left: -19px;
  transform: scale(-1, 1) translate3d(-50%, -50%, 0);
}
@media (max-width: 767px) {
  .faq__btn--left {
    top: -56px;
    transform: scale(-1, 1) translate3d(0, 0, 0);
    left: 4px;
  }
}
.faq__title {
  font-size: 40px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  margin: 0 0 31px 0;
}
@media (max-width: 767px) {
  .faq__title {
    font-size: 30px;
    line-height: 32px;
    margin-bottom: 65px;
  }
}
.faq__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: max-content;
}
.faq__item {
  width: 282px;
  min-height: 160px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.0509803922);
  border-radius: 16px;
}
.faq__item-link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.faq__item-link svg {
  width: 104px;
  height: 162px;
  flex-shrink: 0;
}
.faq__item-info {
  padding: 16px;
}
.faq__item-title {
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  margin: 0 0 9px 0;
}
.faq__item-text {
  font-size: 15px;
  font-weight: 300;
  line-height: 21px;
  color: #949494;
  margin: 0;
}

