.article {
  padding: 22px 0 80px 0;

  &__title {
    font-size: 24px;
    font-weight: 900;
    line-height: 29px;
    margin: 0 0 16px 0;

    @include vp-767 {
      font-size: 20px;
    }
  }

  &__content-list {
    list-style: none;
    padding: 0;
    margin: 0 0 38px 0;
    max-width: 690px;

    li {
      font-size: 15px;
      font-weight: 700;
      line-height: 21px;
      letter-spacing: -0.15px;
    }
  }

  &__btn-share {
    display: inline-block;
    margin-left: 13px;
    width: 24px;
    height: 24px;
    position: relative;

    @include vp-767 {
      width: 16px;
      height: 16px;
      margin-left: 10px
    }

    svg {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -45%, 0);

      @include vp-767 {
        width: 16px;
        height: 16px;
      }
    }
  }

  &__help {
    font-size: 13px;
    font-weight: 700;
    line-height: 16px;
    margin: 0 0 16px 0;
  }

  &__btn-text {
    min-width: 224px;
    padding: 15px;
    display: flex;
    background-color: #36ba5f;
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    color: $color-default-white;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 8px;

    svg {
      width: 16px;
      height: 16px;
      order: -1;
      margin-right: 10px;
    }

    @include vp-767 {
      min-width: 174px;
    }
  }

  &__btn-area {
    display: flex;
    flex-wrap: wrap;
  }

  &__btn {
    width: 48px;
    height: 48px;
    margin-left: 8px;
    border-radius: 12px;
    margin-bottom: 8px;

    svg {
      width: 100%;
      height: 100%;
    }
  }


}
